export default async function* gen<Item = unknown>(
  reader: ReadableStreamDefaultReader,
  { signal }: { signal?: AbortController['signal'] } = {},
): AsyncGenerator<Item, void> {
  const matcher = /\r?\n/;
  const decoder = new TextDecoder();
  let shouldAbort = false;

  signal?.addEventListener('abort', () => {
    shouldAbort = true;
  });

  let buf = '';

  let next = reader.read();

  while (true) {
    if (shouldAbort) {
      console.log('Aborting JSON line by line parsing');
      break;
    }

    const { done, value } = await next;

    if (done) {
      if (buf.length > 0) {
        yield JSON.parse(buf);
      }

      return;
    }

    const chunk = decoder.decode(value, { stream: true });
    // debug(`chunk=${chunk}`);
    buf += chunk;

    const parts = buf.split(matcher);
    buf = parts.pop() ?? '';
    for (const i of parts) {
      yield JSON.parse(i);
    }

    next = reader.read();
  }
}

export async function* fetchNDJSONBatch<T = unknown>(
  url: string | Array<string>,
  {
    batchSize = 100,
    limit,
    onBatchDownloaded,
    abortController,
  }: {
    batchSize?: number;
    limit?: number;
    onBatchDownloaded?: (total: number) => void;
    abortController?: AbortController;
  } = {},
) {
  const urls = Array.isArray(url) ? url : [url];
  let totalBatches = 0;
  let totalItems = 0;

  let shouldAbort = false;

  if (abortController) {
    abortController.signal.addEventListener('abort', () => {
      shouldAbort = true;
    });
  }

  for (const url of urls) {
    console.log(`Fetching data: ${url}`);
    const res = await fetch(url, {
      cache: 'force-cache',
      signal: abortController?.signal,
    });
    const reader = res.body?.getReader();
    if (!reader) {
      throw new Error(`Unable to create reader`);
    }

    const iterator = gen<T>(reader);
    console.log(`Created asyncIterator: url=${url}`);

    let currentBatch: Array<T> = [];
    for await (const res of iterator) {
      if (shouldAbort) {
        console.log(`Aborting dataset loading: url=${url}`);
        throw new Error('Request aborted !');
      }

      if (onBatchDownloaded && totalItems % 100 === 0) {
        onBatchDownloaded(totalItems);
      }

      currentBatch.push(res);
      totalItems++;

      if (totalItems === limit) {
        console.log(`Limit of ${limit} items reached, returning`);
        yield currentBatch;
        iterator.return();
        return;
      }

      if (currentBatch.length === batchSize) {
        console.log(
          `Yielded batch #${totalBatches++}, ${totalBatches * batchSize} total`,
        );

        yield currentBatch;
        currentBatch = [];
      }
    }

    console.log(
      `Yielded final batch #${totalBatches}, ${
        totalBatches * batchSize + currentBatch.length
      } total`,
    );

    if (onBatchDownloaded) {
      onBatchDownloaded(totalItems);
    }
    yield currentBatch;
  }
}
