import type { TrajectoryPlot, ColorFilterState, RGBAColor } from '../colors';
import { createCatchAllFilter, createFilterMatcher } from '../colors/utils';
import { TrajectoryChunk } from './DatasetCache';

const CATCH_ALL_FILTER = createCatchAllFilter('normal');
CATCH_ALL_FILTER.color = [0xff, 0, 0, 0xff];
const TRANSPARENT: RGBAColor = [0, 0, 0, 0];

export class ColorProcessor {
  private colorMatchers: Array<{
    filter: ColorFilterState;
    matcher: ReturnType<typeof createFilterMatcher>;
  }> = [
    {
      filter: CATCH_ALL_FILTER,
      matcher: createFilterMatcher(CATCH_ALL_FILTER),
    },
  ];

  public computeColorChunk = ({
    chunkIndex,
    chunk,
  }: {
    chunkIndex: number;
    chunk: TrajectoryChunk;
  }) => {
    const start = performance.now();
    console.log(
      `[ColorProcessor.computeColorCunk] Computing colors for chunk #${chunkIndex}, length=${chunk.rawItems.length}`,
    );
    if (!chunk) {
      // We might want to throw an error here ?
      console.warn(
        `[ColorProcessor.computeColorCunk] Chunk #${chunkIndex} could not be found ?`,
      );
      return;
    }

    const length = chunk.timestampOffsets.length;
    const colors = new Uint8Array(length * 4);

    const itemIndices = chunk.startIndices;

    // const altitudeRange = [(377 * 100) / 3.2808, (383 * 100) / 3.2808];

    for (let itemIndex = 0; itemIndex < chunk.rawItems.length; itemIndex++) {
      const currentItem = chunk.rawItems[itemIndex];
      const startIndex = itemIndices[itemIndex];
      const endIndex = itemIndices[itemIndex + 1] ?? length;
      const color = this.getColorForItem(currentItem);

      for (let i = startIndex; i < endIndex; i++) {
        if (typeof color === 'function') {
          const longitude = chunk.positions[i * 3];
          const latitude = chunk.positions[i * 3 + 1];
          const altitudeM = chunk.positions[i * 3 + 2];

          const c = color({ latitude, longitude, altitudeM });
          colors[i * 4] = c[0];
          colors[i * 4 + 1] = c[1];
          colors[i * 4 + 2] = c[2];
          colors[i * 4 + 3] = c[3];
        } else {
          colors[i * 4] = color[0];
          colors[i * 4 + 1] = color[1];
          colors[i * 4 + 2] = color[2];
          colors[i * 4 + 3] = color[3];
        }
      }
    }

    const end = performance.now() - start;
    console.log(
      `[ColorProcessor.computeColorChunk] Chunk #${chunkIndex} took ${end}ms`,
    );
    return colors;
  };

  public setColorFilters = (filters: Array<ColorFilterState>) => {
    this.colorMatchers = filters.map((filter) => ({
      filter,
      matcher: createFilterMatcher(filter),
    }));
  };

  private getColorForItem(
    item: TrajectoryChunk['rawItems'][number],
  ): RGBAColor | ((plot: TrajectoryPlot) => RGBAColor) {
    /**
     * This should never happen as there should always be a catchall filter
     * in the last position.
     */
    if (this.colorMatchers.length === 0) {
      return CATCH_ALL_FILTER.color;
    }

    const plotMatchers: Array<{
      filter: ColorFilterState;
      plotMatcher: (plot: TrajectoryPlot) => RGBAColor | boolean;
    }> = [];
    let itemMatcherColor: RGBAColor | undefined;

    /**
     * Matcher return value can either be a boolean or a function.
     *
     * This function is a plot matcher, taking each trajectory plot and returning a color.
     */
    for (const { filter, matcher } of this.colorMatchers) {
      if (!filter.enabled) {
        continue;
      }

      const matcherOutput = matcher(item);

      if (typeof matcherOutput === 'function') {
        plotMatchers.push({ filter, plotMatcher: matcherOutput });
      }

      if (matcher(item) === true) {
        if (plotMatchers.length === 0) {
          return filter.color;
        }

        itemMatcherColor = filter.color;
      }
    }

    if (plotMatchers.length > 0) {
      return (plot: TrajectoryPlot) => {
        for (const { filter, plotMatcher } of plotMatchers) {
          const output = plotMatcher(plot);
          /**
           * No match for this plot, continue
           */
          if (output === false) {
            continue;
          }

          if (output === true) {
            return filter.color;
          }

          return output;
        }

        return itemMatcherColor ?? TRANSPARENT;
      };
    }
    /**
     * If nothing matches, make it transparent
     */
    return TRANSPARENT;
  }
}
