/* eslint-disable no-restricted-globals */
import type { DatasetLoadOptions, DatasetMetadata } from '../Loader/hooks';
import type { TrajectoryItem } from '../scripts/parseTrajectory';
import { DatasetCache } from './DatasetCache';
import type { ColorFilterState } from '../colors';
import { ColorProcessor } from './ColorProcessor';

export {};

export type DatasetLoadProgress = {
  flights: number;
  drawnDataPoints: number;
  totalDataPoints: number;
  downloadedFlights: number;
  countByAo: { [key: string]: number };
};

export type MainToWorkerMessage =
  | {
      type: 'LOAD_START';
      loadOptions: DatasetLoadOptions;
      metadata: DatasetMetadata;
    }
  | { type: 'LOAD_ABORT' }
  | { type: 'SET_COLOR_RULES'; rules: Array<ColorFilterState> };

export type WorkerToMainMessage =
  | { type: 'LOAD_START' }
  | {
      type: 'LOAD_CHUNK';
      idx: number;
      chunk: {
        rawItems: Array<
          Omit<TrajectoryItem, 'timestampOffsets' | 'coordinates'>
        >;
        positions: Float32Array;
        timestampOffsets: Float32Array;
        startIndices: Uint32Array;
        squawks: Array<number | null>;
      };
    }
  | {
      type: 'LOAD_PROGRESS';
      statistics: DatasetLoadProgress;
    }
  | { type: 'LOAD_DONE' }
  | { type: 'LOAD_ERROR'; errorMessage: string }
  | { type: 'COLOR_START' }
  | { type: 'COLOR_ERROR'; errorMessage: string }
  | {
      type: 'COLOR_CHUNK';
      chunkIndices: Array<number>;
      colors: Array<Uint8Array>;
    }
  | { type: 'COLOR_DONE' };

self.onmessage = handleInboundMessage;

const datasetCache = new DatasetCache();
const colorProcessor = new ColorProcessor();

let abortController = new AbortController();
let colorAbortController = new AbortController();

async function handleInboundMessage(
  message: MessageEvent<MainToWorkerMessage>,
) {
  console.log('[worker] Message received !', message);
  const { data } = message;

  switch (data.type) {
    case 'LOAD_START': {
      if (abortController) {
        abortController.abort();
        abortController = new AbortController();
      }

      /**
       * Cancel running color computing
       */
      if (colorAbortController) {
        colorAbortController.abort();
        colorAbortController = new AbortController();
      }

      console.log(
        '[worker] Starting dataset load',
        data.metadata,
        data.loadOptions,
        self.location,
      );

      const { metadata, loadOptions } = data;

      postMessage({ type: 'COLOR_START' });
      postMessage({ type: 'LOAD_START' });

      try {
        await datasetCache.start({
          metadata,
          loadOptions,
          abortController,
          onChunkLoaded: (baseChunk, idx) => {
            /**
             * We're creating new typed arrays for the main thread since we need to keep a hold of them
             * to compute colors
             */
            const chunk = {
              ...baseChunk,
              positions: baseChunk.positions.slice(0),
              timestampOffsets: baseChunk.timestampOffsets.slice(0),
            };

            postMessage(
              {
                type: 'LOAD_CHUNK',
                idx,
                chunk,
              },
              [chunk.positions.buffer, chunk.timestampOffsets.buffer],
            );

            let colors = colorProcessor.computeColorChunk({
              chunk: baseChunk,
              chunkIndex: idx,
            });

            if (!colors) {
              return;
            }

            colors = colors.slice();

            postMessage(
              {
                type: 'COLOR_CHUNK',
                chunkIndices: [idx],
                colors: [colors],
              },
              [colors.buffer],
            );
          },
          onProgress: (statistics) => {
            postMessage({ type: 'LOAD_PROGRESS', statistics });
          },
        });
      } catch (err: any) {
        postMessage({ type: 'LOAD_ERROR', errorMessage: err.message });
        postMessage({ type: 'COLOR_ERROR', errorMessage: err.message });
      }

      console.log('[worker] Loading done !');
      postMessage({ type: 'LOAD_DONE' });
      postMessage({ type: 'COLOR_DONE' });

      return;
    }
    case 'LOAD_ABORT': {
      if (abortController) {
        abortController.abort();
        abortController = new AbortController();
      }

      return;
    }
    case 'SET_COLOR_RULES': {
      if (colorAbortController) {
        console.log('[worker]: Aborting previous run');
        colorAbortController.abort();
        colorAbortController = new AbortController();
      }

      const signal = colorAbortController.signal;

      console.log(`[worker]: Setting color filters`);
      colorProcessor.setColorFilters(data.rules);

      postMessage({ type: 'COLOR_START' });
      let queue: Array<{ idx: number; colors: Uint8Array }> = [];

      let start = performance.now();
      const MAX_WAIT = 1500;
      for (const [idx, chunk] of datasetCache.state.chunks.entries()) {
        if (signal.aborted) {
          console.log('[worker]: Run aborted while not finished !');
          colorAbortController = new AbortController();
          return;
        }

        let colors = colorProcessor.computeColorChunk({
          chunkIndex: idx,
          chunk,
        });

        if (!colors) {
          return;
        }

        queue.push({ idx, colors: colors.slice() });
        const elapsed = performance.now() - start;

        if (elapsed > MAX_WAIT) {
          console.log('[worker]: elapsed > MAX_WAIT, flushing queue');
          postMessage(
            {
              type: 'COLOR_CHUNK',
              chunkIndices: queue.map(({ idx }) => idx),
              colors: queue.map(({ colors }) => colors),
            },
            queue.map(({ colors }) => colors.buffer),
          );
          queue = [];
          start = performance.now();
        }

        await delay(0);
      }

      if (queue.length) {
        postMessage(
          {
            type: 'COLOR_CHUNK',
            chunkIndices: queue.map(({ idx }) => idx),
            colors: queue.map(({ colors }) => colors),
          },
          queue.map(({ colors }) => colors.buffer),
        );
      }

      postMessage({ type: 'COLOR_DONE' });
      return;
    }
    default:
      console.log(`[worker]: Unhandled message type ${(data as any).type}`);
  }
}

function postMessage(
  message: WorkerToMainMessage,
  transferable?: Array<Transferable>,
) {
  if (transferable) {
    self.postMessage(message, transferable);
  } else {
    self.postMessage(message);
  }
}

function delay(d: number): Promise<void> {
  return new Promise((resolve) => setTimeout(resolve, d));
}
